@import '../../styles//index.scss';

.usercard {
    width: 380px;
    height: 460px;
    border-radius: 20px;
    background: linear-gradient(114.99deg, #471CA9 -0.99%, #5736A3 54.28%, #4B2A99 78.99%);
    margin-left: auto;
    margin-right: auto;

    &header {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 214px;
    };

    &logo {
      position: absolute;
      width: 76px;
      height: 22px; 
      margin-left: 20px;
      margin-top: 20px;
    };

    &svg{
        width: 308px;
        height: 168px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 28px;
    };

    &stripe{
        display: block;
        width: 380px;
        height: 8px;
        background: #EBD8FF;
    };
};